@import url(https://fonts.googleapis.com/css2?family=Goldman&family=Press+Start+2P&display=swap);
body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  background-color: #ECECEA;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: transparent;
  border: 1px solid transparent;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 48px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 48px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game-info {
  margin-left: 20px;
}


.icons-attribution, .game-status, .fallen-soldier-block{
  margin-top: 20px;
  min-height: 50px;
}



/*Board color scheme Wheat from http://omgchess.blogspot.com/2015/09/chess-board-color-schemes.html*/
.dark-square{
background-color: RGB(187,190,100);
}

.light-square{
    background-color: RGB(234,240,206);
}

#player-turn-box{
  width: 32px;
  height: 32px;
  border: 1px solid #000;
  margin-bottom: 10px;
}

h3{
  margin-bottom: 5px;
}

#chat-Room-Area{
  overflow-y:scroll;
}
.chat-room-container {
    float: right;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 25%;
    
}

.room-name {
    margin-top: 0;
}

.messages-container {
    flex: 1 1;
    min-height: 60vh;
    max-height: 60vh;
    overflow: auto;
    border: 1px solid black;
    border-radius: 7px 7px 0 0;
    position: relative;
    background-color: red;

}

.messages-list {
    list-style-type: none;
    overflow-y:scroll;
    overflow:hidden;
    padding: 0;
}



.message-item {
    width: 55%;
    margin: 8px;
    padding: 12px 8px;
    word-break: break-word;
    border-radius: 4px;
    color: white;
}

.my-message {
    background-color: rgb(0, 132, 255);
    margin-left: auto;
    position: relative;
    font-family: "Press Start 2P";
    text-align: right;

}

.received-message {
    background-color: #3f4042;
    margin-right: auto;
    font-family: "Press Start 2P";
    text-align: left;

}

.send-message-button {
    max-height: 10%;
    min-height: 10%;
    width: 100px;
    height: 75px;
    font-size: 20px;
    font-weight: 600;
    color: white;
    background-color: blue;
    padding: 24px 12px;
    border: none;
    resize: none;
    display:inline-block;
    vertical-align: top;
    font-family: "Press Start 2P";

    
}

.send-message-button:hover {
    color: red;
  }


.new-message-input-field {
    height: 58px; 
    width:73%;
    max-height: 20%;
    min-height: 20%;
    font-size: 20px;
    padding: 8px 12px;
    background-color: #3f4042;
    display:inline-block;
    vertical-align: top;
    resize: none;
    color: white;
    font-family: "Press Start 2P";


}

.new-message-input-field::-webkit-input-placeholder {
    font-family: "Press Start 2P";
    font-size: 20px;
}

.new-message-input-field:-ms-input-placeholder {
    font-family: "Press Start 2P";
    font-size: 20px;
}

.new-message-input-field::-ms-input-placeholder {
    font-family: "Press Start 2P";
    font-size: 20px;
}

.new-message-input-field::placeholder {
    font-family: "Press Start 2P";
    font-size: 20px;
}
.messages-container, .new-message-input-field, .send-message-button {
    border-color: #9a9a9a;
}

#chat-Room-Area{
    overflow-y:scroll;
  }

  h1{
  text-align: left;
  font-family: "Press Start 2P";

  }

  .combinedDiv {
    text-align: left;
   left: -50px;
}

.outer-container{
    text-align: center;
}

.join-wrapper{
    width: 75%;
    display: inline-block;
    text-align: center;
}
